import React from 'react';
import { TabBar } from 'antd-mobile';
import Home from './Home';
import Inspiration from './Inspiration';
import Shop from './Shop';

export default class AppTab extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedTab: 'home',
    };
  }
  render() {
    return (
      <div style={{ position: 'fixed', height: '100%', width: '100%', top: 0 }}>
        <TabBar
          unselectedTintColor="#949494"
          tintColor="#3fcccb"
          barTintColor="white"
        >
          <TabBar.Item
            title="首页"
            key="home"
            icon={<i style={{fontSize:22}} class="iconfont iconshouye"></i>}
            selectedIcon={<i style={{fontSize:22}} class="iconfont iconshouye"></i>}
            selected={this.state.selectedTab === 'home'}
            onPress={() => {
              this.setState({
                selectedTab: 'home',
              });
            }}
            data-seed="logId"
          >
        	  <Home />
          </TabBar.Item>
          <TabBar.Item
            icon={<i style={{fontSize:22}} class="iconfont iconlinggan"></i>}
            selectedIcon={<i style={{fontSize:22}} class="iconfont iconlinggan"></i>}
            title="灵感"
            key="linggan"
            selected={this.state.selectedTab === 'inspiration'}
            onPress={() => {
              this.setState({
                selectedTab: 'inspiration',
              });
            }}
          >
            <Inspiration/>
          </TabBar.Item>
          <TabBar.Item
            icon={<i style={{fontSize:22}} class="iconfont iconstore_icon"></i>}
            selectedIcon={<i style={{fontSize:22}} class="iconfont iconstore_icon"></i>}
            title="商城"
            key="shop"
            selected={this.state.selectedTab === 'shop'}
            onPress={() => {
              this.setState({
                selectedTab: 'shop',
              });
            }}
          >
            <Shop/>
          </TabBar.Item>
          <TabBar.Item
            icon={<i style={{fontSize:22}} class="iconfont iconwode"></i>}
            selectedIcon={<i style={{fontSize:22}} class="iconfont iconwode"></i>}
            title="我的"
            key="my"
            selected={this.state.selectedTab === 'my'}
            onPress={() => {
              this.setState({
                selectedTab: 'my',
              });
            }}
          >
          </TabBar.Item>
        </TabBar>
      </div>
    );
  }
}