import React, { Component } from 'react'
import {NavBar,Icon,Tabs,Grid} from 'antd-mobile';

const tabs = [
    { title: '推荐' },
    { title: '冬季' },
    { title: '宜家' },
    { title: '小清新' },
    { title: '小户型' },
    { title: '个性色彩' },
    { title: '个性' },
    { title: '个性' },
    { title: '个性' },
  ];
  const data = [
    {icon:'img/linggan2.jpg'},
    {icon:'img/linggan3.jpg'},
    {icon:'img/linggan4.jpg'},
    {icon:'img/linggan5.jpg'},
    {icon:'img/linggan6.jpg'},
    {icon:'img/linggan7.jpg'},
];
export default class Inspiration extends Component {    
    render() {
        return (
            <div style={{lineHeight: 0}}>
                <img src="img/linggan1.jpg" style={{maxWidth:'100%',maxHeight:'100%'}} />
                <NavBar
                    style={{backgroundColor:'#3fcccb',color:'#fff'}}
                    rightContent={[
                        <Icon key="0" type="search"/>,
                      ]}
                    >
                    灵感
                </NavBar>
                <Tabs tabs={tabs}
                    initialPage={0}
                    destroyInactiveTab={true}
                    tabBarUnderlineStyle={{border:'none'}}
                    tabBarActiveTextColor={'#3fcccb'}
                    >
                        {
                            tabs.map(item=>{
                                if(item.title=='推荐'){
                                    return  <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center', backgroundColor: '#fff'}}>
                                                <Grid
                                                    data={data}
                                                    square={false}
                                                    className="not-square-grid"
                                                    columnNum={2}
                                                    renderItem={dataItem => (
                                                        <div style={{width:'100%',height:180}}>
                                                            <img src={dataItem.icon} style={{maxWidth:'100%',maxHeight:'100%'}} />
                                                        </div>
                                                    )}
                                                    />
                                            </div>
                                }else{
                                    return <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center', height: '150px', backgroundColor: '#fff' }}>
                                        {item.title}
                                    </div>
                                }
                            })
                        }
                </Tabs>
            </div>
        )
    }
}
