import React, { Component } from 'react'
import {NavBar,Carousel,Grid,Card} from 'antd-mobile';

// const tabs = [
//     { title: '推荐' },
//     { title: '家具' },
//     { title: '餐厨' },
//     { title: '床上用品' },
//   ];
const data = [
    {icon:'img/home5.jpg'},
    {icon:'img/home4.jpg'},
    {icon:'img/home3.jpg'}
];
export default class Home extends Component {
    constructor(){
        super();
        this.state = {
            data:['img/home1.jpg','img/home2.jpg']
        }
    }
    
    render() {
        return (
            <div style={{lineHeight: 0}}>
                <img src="img/linggan1.jpg" style={{maxWidth:'100%',maxHeight:'100%'}} />
                <NavBar
                    style={{backgroundColor:'#3fcccb',color:'#fff'}}
                    >
                    住吧家居
                </NavBar>
                <div style={{height: '170px'}}>
                    <Carousel
                        style={{backgroundColor:"#fff"}}
                        autoplay={false}
                        infinite
                        dotStyle={{width:20,height:2,borderRadius:1,backgroundColor:'#fff',marginBottom:20}}
                        dotActiveStyle={{width:20,height:2,borderRadius:1,backgroundColor:'#3fcccb',marginBottom:20}}
                        >
                        {this.state.data.map(val => (
                            <img
                                src={val}
                                alt=""
                                style={{ width: '100%', verticalAlign: 'top' }}
                                onLoad={() => {
                                window.dispatchEvent(new Event('resize'));
                                this.setState({ imgHeight: 'auto' });
                                }}
                            />
                        ))}
                    </Carousel>
                </div>
                <div style={{backgroundColor: '#ebebef'}}>
                    <Grid data={data}
                        columnNum={3}
                        itemStyle={{
                            padding:0,textAlign:'center',
                            backgroundColor: '#ebebef'
                        }}
                        renderItem={dataItem => (
                            <img src={dataItem.icon} style={{ width: '85%', height: '100%',padding:0}} alt="" />
                        )}
                        hasLine={false}
                    />
                </div>
                <div>
                <Card>
                    <Card.Header
                        style={{paddingLeft:0}}
                        title="热门推荐"
                        thumb={<div style={{backgroundColor:'#3fcccb',marginRight:10,width:3,height:25}}></div>}
                    />
                    <Card.Body style={{borderTop:'none'}}>
                            <img src="img/home6.jpg" style={{maxWidth:'100%',maxHeight:'100%'}}/>
                    </Card.Body>
                </Card>
                </div>
            </div>
        )
    }
}
