import React, { Component } from 'react'
import {NavBar,Grid,Carousel,SearchBar} from 'antd-mobile';

const data = [
    {icon:'img/shop1.jpg'},
    {icon:'img/shop1.jpg'},
    {icon:'img/shop1.jpg'},
    {icon:'img/shop1.jpg'}
];
const data1 = [
    {icon:'img/shop2.jpg',text:'桌'},
    {icon:'img/shop3.jpg',text:'床'},
    {icon:'img/shop4.jpg',text:'椅'},
    {icon:'img/shop5.jpg',text:'几'},
    {icon:'img/shop6.jpg',text:'柜'},
    {icon:'img/shop7.jpg',text:'书架'},
    {icon:'img/shop8.jpg',text:'沙发'},
    {icon:'img/shop9.jpg',text:'家居饰品'},
    {icon:'img/shop10.jpg',text:'户外家具'},
    {icon:'img/shop11.jpg',text:'全部分类'},
]
const data2 = [
    {icon:'img/shop13.jpg',text1:'Top Art Studio 欧式风格精细...',text2:'￥39.95'},
    {icon:'img/shop14.jpg',text1:'顺顺工艺欧式风格塑料外框黑...',text2:'￥83.99'}
]
export default class Shop extends Component {    
    render() {
        return (
            <div style={{lineHeight: 0}}>
                <img src="img/linggan1.jpg" style={{maxWidth:'100%',maxHeight:'100%'}} />
                <NavBar
                    style={{backgroundColor:'#3fcccb',color:'#fff'}}
                    rightContent={[
                        <div style={{fontSize:20}} key="0" className="iconfont icongouwuche2"/>,
                      ]}
                    >
                    商城
                </NavBar>
                <div style={{height: '160px',position:"relative",marginBottom:30}}>
                    <Carousel
                        style={{backgroundColor:"#fff"}}
                        autoplay={false}
                        infinite
                        dotStyle={{backgroundColor:'#fff',marginBottom:10}}
                        dotActiveStyle={{backgroundColor:'#3fcccb',marginBottom:10}}
                        >
                        {data.map(val =>(
                                <img
                                    src={val.icon}
                                    alt=""
                                    style={{ width: '100%', verticalAlign: 'top' }}
                                    onLoad={() => {
                                    window.dispatchEvent(new Event('resize'));
                                    this.setState({ imgHeight: 'auto' });
                                    }}
                                />
                        ))}
                    </Carousel>
                    <div style={{position:"absolute",top:0,width:'100%',height:45,backgroundColor:"#848484",opacity:0.9}}>
                        <div style={{width:'100%',float:'left'}}><span style={{position:'absolute',top:23,left:16,color:"#fff"}} className="iconfont iconxingzhuang"></span><SearchBar placeholder="输入关键字搜索" maxLength={6} style={{width:'85%',float:'right',padding:0,margin:0,backgroundColor:"#848484"}}/></div>
                    </div>
                </div>
                <div>
                    <Grid
                        data={data1}
                        square={false}
                        hasLine={false}
                        className="not-square-grid"
                        columnNum={5}
                        renderItem={dataItem => (
                            <div>
                                <img src={dataItem.icon} style={{maxWidth:'60%',maxHeight:'40%'}} />
                                <div style={{marginTop:12,fontSize:8}}>{dataItem.text}</div>
                            </div>
                        )}
                        />
                </div>
                <div style={{width:'100%'}}>
                    <Grid
                        data={data2}
                        square={false}
                        hasLine={false}
                        className="not-square-grid"
                        columnNum={2}
                        style={{padding:0,backgroundColor:"#eee"}}
                        renderItem={dataItem => (
                            <div style={{backgroundColor:"#eee",padding:10,width:'100%',height:160}}>
                                <img src={dataItem.icon} style={{maxWidth:'100%',maxHeight:'100%'}} />
                                <div style={{marginTop:12,fontSize:11,float:'left',color:'#8e8e8e'}}>{dataItem.text1}</div>
                                <div style={{marginTop:12,fontSize:8,float:'left'}}>{dataItem.text2}</div>
                            </div>
                        )}
                        />
                </div>
            </div>
        )
    }
}
